#three-dot {
  display: none;
  position: absolute;
  right: 20px;
}

#dot-option {
  position: absolute;
  z-index: 5;
}

.three-dot-container {
  position: absolute;
}

.dot-option-container {
  position: absolute;
  width: 150px;
  height: 182.9px;
  right: 10px;
  top: -30px;
}

.dot-option-box {
  position: absolute;
  font-size: 15px;
  height: inherit;
  width: inherit;
  background-color: var(--secondary);
}

.dot-option-content {
  position: absolute;
  height: inherit;
  width: inherit;
}

.dot-option-content div {
  padding-top: 10px;
  border-bottom: 1px solid var(--border);
}

.dot-option-content div span {
  position: relative;
  left: 10px;
  top: -5px;
}

.dot-option-content div {
  height: 35px;
}

.dot-option-content div:hover {
  background-color: var(--hover1);
}
