.audio-wrapper {
  width: 100;
  position: absolute;
  bottom: 0px;
  z-index: 999;
}

.audio-container {
  bottom: 0px;
  width: calc(100% - var(--nav-width));
  height: 50px;
  position: fixed;
}

.audio-box {
  height: 50px;
  width: 100%;
  position: fixed;
  background-color: var(--secondary);
}

.progress_div {
  position: absolute;
  width: 70%;
  left: 20%;
  height: 30px;
  top: 20px;
}

.progress_holder {
  position: absolute;
  width: 80%;
  height: 2px;
  background-color: var(--border);
  border: 0.5px solid var(--border2);
  border-radius: 30px;
}

#progress_meter {
  position: absolute;
  height: 2px;
  background-color: var(--font2);
}

.play_btn-container {
  position: absolute;
  width: calc(4% + 30px);
  height: 50px;
  display: flex;
  justify-content: center;
  left: 8%;
}

#play_btn {
  cursor: pointer;
  user-select: none;
  position: absolute;
  top: 4px;
  font-size: 25px;
  left: 5%;
}

.play-container {
  width: 28%;
  height: calc(2vw + 1px);
  position: absolute;
  top: 7px;
}

#fast_forward {
  position: absolute;
  right: 0%;
  font-size: 20px;
  top: 13px;
  cursor: pointer;
  user-select: none;
}

#fast_rewind {
  position: absolute;
  left: 0%;
  font-size: 20px;
  top: 13px;
  cursor: pointer;
  user-select: none;
}

.volume {
  position: absolute;
  display: flex;
  right: 1%;
  width: 140px;
  height: 100px;
}

.volume-i {
  cursor: pointer;
  position: absolute;
  bottom: 60px;
  height: 30px;
  width: 300px;
  user-select: none;
}

.volume_slider {
  display: flex;
  position: absolute;
  width: 90px;
  height: 20px;
  user-select: none;
  right: 15px;
  bottom: 70px;
  justify-content: center;
  background: var(--secondary);
}

.volume_slider input {
  cursor: pointer;
  position: absolute;
  top: 8px;
  -webkit-appearance: none;
  appearance: none;
  width: 100%;
  height: 3px;
  background: var(--border);
  outline: none;
  -webkit-transition: 0.2s;
  transition: opacity 0.2s;
  border-radius: 5px;
}

.volume_slider input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 10px;
  width: 10px;
  border-radius: 100px;
  background: var(--font2);
  cursor: pointer;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
}

.audio_elements-container {
  position: absolute;
  display: flex;
  width: 94%;
  height: 70px;
  left: 100px;
  justify-content: center;
}

#audio-preview {
  position: absolute;
  font-size: 14px;
}

.audio-preview-container {
  position: absolute;
  display: flex;
  width: 150px;
  height: 50px;
}

.audio-preview-contents {
  position: absolute;
  display: flex;
  padding: 5px;
}

.audio-preview-context {
  position: absolute;
  width: 100px;
  left: 60px;
  overflow-x: hidden;
  overflow-y: visible;
  height: 50px;

  font-size: 12px;
}

.audio-preview-box {
  width: inherit;
  height: inherit;
}

.audio-preview-image {
  position: absolute;
  width: 45px;
  height: 45px;
  top: 2.5px;
}

.audio-preview-image .image {
  position: absolute;
  width: inherit;
  height: inherit;
  background-size: 45px 45px;
}

.audio-preview-title {
  position: absolute;
  height: 21px;
  overflow: hidden;
  width: 100px;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.audio-preview-title span {
  width: 100px;
}

.audio-preview-artist {
  position: relative;
  top: 20px;
}

.audio-preview-like i {
  font-size: 5px;
}

.current,
.duration {
  position: absolute;
  font-size: 12px;
  top: 13px;
}

.duration {
  right: 21%;
}

.current {
  left: 17%;
}

@media screen and (max-width: 600px) {
  .audio-container {
    margin-left: 0px;
    height: 30px;
    bottom: 35px;
  }
  .audio-box {
    height: 30px;
  }
  .progress_div {
    top: 10px;
    width: 50%;
    left: 30vw;
  }
  .volume {
    display: none;
  }
}
