#userLikes-music {
  top: 0px;
}

#userLikes {
  margin-top: 0px;
}

.top {
  position: absolute;
  top: 60px;
  width: 100%;
  height: 18%;
}

.top-listen {
  position: absolute;
  top: 60px;
  width: 100%;
  height: 30%;
}

.bg-blur {
  position: absolute;
  width: 100%;
  height: 100%;
  background-size: 100% 100%;
  filter: blur(8px);
}

.top-container {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
}

.top-contents {
  position: absolute;
  width: 100%;
  height: 100%;
  padding-left: 20px;
  padding-right: 20px;
}

.top-info {
  position: absolute;
  left: 250px;
  top: 100px;
}

.top-albumCover-wrapper {
  position: absolute;
  width: 13rem;
  height: 13rem;
  border: 1px solid var(--primary);
  box-shadow: 1px 0px 3px rgb(0, 0, 0);
  top: 50px;
}

.top-artist-name {
  color: var(--font2);
}

.top-title h2 {
  color: #fff;
  font-weight: 900;
  margin: 0px;
}

.top-play-botton {
  position: absolute;
  top: 230px;
  left: 250px;
}

.top-play-botton button {
  background-color: var(--button2);
  border: 0px;
  outline: none;
  color: #fff;
  width: 75px;
  height: 25px;
  border-radius: 100px;
  cursor: pointer;
}

.top-albumCover {
  position: absolute;
  width: 13rem;
  height: 13rem;
  background-size: 13rem 13rem;
}

.top-description {
  color: var(--font2);
}
