#nav {
  width: var(--nav-width);
  background-color: var(--primary);
  margin-top: 0px;
  position: fixed;
  color: white;
  height: calc(100vh - 50px);
  top: 0px;
  margin-right: 0px;
}

.btn {
  background-repeat: no-repeat;
  transform: translateX(-20px);
}

nav li {
  color: var(--font1);
  list-style: none;
  background-size: contain;
  display: flex;
  left: 0px;
  transform: translateX(-30px);
  position: sticky;
  height: 40px;
  width: 50px;
  margin-right: 0px;
}

nav ul {
  padding: 0px;
  padding-left: 40px;
}

.main-nav {
  position: absolute;
  top: 70px;
  overflow-y: scroll;
  overflow-x: hidden;
  width: var(--nav-width);
  height: 75%;
  scrollbar-width: thin;
  scrollbar-color: transparent transparent;
}

.main-nav::-webkit-scrollbar {
  width: 5px;
}

.main-nav::-webkit-scrollbar-track {
  background: transparent;
}
.main-nav::-webkit-scrollbar-thumb {
  background-color: var(--thumbBG);
  border-radius: 6px;
}

nav li:hover {
  color: var(--button2);
}

nav li p {
  margin: 0px;
  margin-left: 10px;
  font-size: 14px;
}
nav a {
  text-decoration: none;
}

.btn-active span {
  color: var(--button2);
}

.btn-active p {
  color: var(--button2);
}

.btn span {
  font-size: 20px;
}

.signout {
  position: absolute;
  bottom: 10px;
  left: 20px;
}

.signout span {
  cursor: pointer;
}

.nav-heading {
  position: relative;
  left: 20px;
  color: var(--font2);
  font-size: 14px;
}

.nav-heading h2 {
  filter: drop-shadow(0 0 10px var(--button2));
  color: var(--button2);
}

#nav-line {
  width: 130px;
  left: -20px;
  position: absolute;
  top: 201px;
  border-bottom: 1px solid var(--border2);
}

@media screen and (max-width: 600px) {
  nav ul {
    display: inline-flex;
  }
  #nav {
    position: relative;
    top: auto;
    width: 100vw;

    margin-top: 0px;
    position: fixed;
    color: white;
    height: 35px;
    bottom: 0px;
    margin-right: 0px;
  }
  nav li {
    width: 80px;
    margin-top: 0px;
  }
  nav ul {
    margin-top: 5px;
  }

  .signout {
    left: auto;
    right: 30px;
    bottom: 0px;
  }
  .btn span {
    font-size: 30px;
  }
}
