#notification {
  position: absolute;
  right: 0px;
  display: flex;
  top: 50px;
}

.notification-container {
  width: 500px;
  min-height: 300px;
  display: flex;
  overflow-y: scroll;
  overflow-x: hidden;
}

.notification-box {
  width: inherit;
  height: inherit;
  background-color: var(--secondary);
}

.notfication-header {
  position: fixed;
  width: inherit;
  height: 40px;
  background-color: var(--primary);
  display: flex;
  justify-content: center;
  z-index: 1;
}

.notfication-header span {
  position: absolute;

  color: #fff;
  font-family: "Aguafina Script", cursive;
  font-size: 33px;
}

.notification-contents {
  background-color: var(--secondary);
  position: relative;
  top: 40px;
}

.notification-items {
  display: inline-flex;
  width: 300px;
  height: 45px;
  padding-left: 10px;
}

.notification-info {
  position: absolute;
  width: 355px;
  height: 45px;
  left: 70px;
  transform: translateY(10px);
}

.notification-userName {
  position: absolute;
}

.notfication-type {
  position: absolute;
  left: 55px;
}

.notification-profile {
  width: 50px;
  height: 50px;
  position: relative;
}

.notification-activity {
  position: absolute;
  left: 135px;
}

#notfication-pfp {
  width: 50px;
  height: 50px;
  position: absolute;
  top: 0px;
}

#notfication-pfp-box {
  width: 50px;
  height: 50px;
  z-index: 0;
}

#notfication-pfp-container {
  position: absolute;
  border-color: transparent;
  height: 50px;
  width: 50px;
  left: -8px;
  top: -5px;
}

#notfication-pfp-img {
  height: 100px;
  top: -45.5px;
}
