#music-card {
  position: relative;
  height: 50px;
  width: 98.2%;
  margin-bottom: 5px;
  cursor: pointer;
}
.music-card-box {
  height: inherit;
  width: inherit;
  position: absolute;
  border: 1px solid var(--border);
  background-color: var(--card);
  backdrop-filter: blur(5px);
  transition: 0.2s;
}

.play-btn {
  transition: 0.5s;
  width: 40px;
  position: absolute;
  top: 4.5px;
  height: 40px;
  background-color: rgba(14, 14, 14, 0.5);
  z-index: 1;
  display: none;
  justify-content: center;
}

.play-btn span {
  position: absolute;
  font-size: 40px;
}

.music-card-box:hover {
  background: var(--hover1);
}

.music-card-box:hover #three-dot {
  display: flex;
}

.music-card-container {
  height: inherit;
  width: inherit;
  display: flex;
  padding: 8px;
}

.music-card-info-container {
  position: absolute;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  height: 80%;
  width: 100%;
  padding-top: 5px;
  left: calc(60px + 1%);
}

.music-card-left {
  position: relative;
  top: 0px;
  width: 200px;
  height: 30px;
  display: inline-flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.music-card-artist,
.music-card-title {
  position: absolute;
  width: auto;
  height: 10px;
}

.music-card-title h5 {
  margin: 0px;
}

.music-card-middle {
  position: relative;
  height: 60px;
  right: 10%;
  display: flex;
  width: 50%;
  flex-wrap: wrap;
}

.music-card-uploader,
.music-card-genre {
  position: relative;
  width: 100px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.music-card-genre {
  width: 80px;
  right: -10%;
}

.music-card-bottom {
  position: relative;
  height: 20px;
  left: 0px;
}

#music-card-cover {
  position: absolute;
  width: 40px;
  top: 4.5px;
  height: 40px;
  box-shadow: 1px 0px 3px rgb(0 0 0);
}

.album-cover {
  top: 0px;
  left: 0px;
  height: inherit;
  width: inherit;
  background: var(--border);
}

.album-cover span {
  position: absolute;
  height: inherit;
  width: inherit;
  background-size: 40px 40px;
}
.music-card-box:hover .play-btn {
  display: flex;
}

.album-cover span:hover .play-btn {
  display: flex;
}
